    height: 100%;
    display: -ms-flexbox;
    display: -webkit-box;
    display: flex;
    -ms-flex-direction: column;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    flex-direction: column;
    -ms-flex-pack: center;
    -webkit-box-pack: center;
    justify-content: center;
    -ms-flex-align: center;
    -webkit-box-align: center;
    align-items: center;
    -ms-flex: 1;
    -webkit-box-flex: 1;
    flex: 1;
}

.uppy-Url input.uppy-u-reset.uppy-c-textInput.uppy-Url-input {
    width: 90% !important;
    max-width: 650px;
    margin-bottom: 15px;
}

.uppy-Dashboard-close {
    top: -10px !important;
    font-size: 35px !important;
    color: #fff !important;
}

.landing_page .shoppable_box {
    padding: 30px;
    border: 1px solid #f1f0f0;
    border-radius: 6px;
    text-align: center;
    width: 100%;
    transition: all 0.25s ease-in-out;
}

.landing_page .card-title {
    margin-bottom: 30px;
}

.landing_page .shoppable_box p {
    color: #333;
    font-size: 12px;
    font-weight: 500;
    margin-bottom: 0;
    font-size: 12px;
    transition: all 0.25s ease-in-out;
}

.landing_page .shoppable_box:hover,
.landing_page .shoppable_box:focus,
.landing_page .shoppable_box:active {
    border-color: #4E69ED;
}

.landing_page .shoppable_box:hover svg path,
.landing_page .shoppable_box:focus svg path,
.landing_page .shoppable_box:active svg path {
    fill: #4E69ED;
    transition: all 0.25s ease-in-out;
}

.landing_page .shoppable_box:hover p,
.landing_page .shoppable_box:focus p,
.landing_page .shoppable_box:active p {
    color:#4E69ED;
}

.landing_page .shoppable_box:active svg path{
    transition: all 0.25s ease-in-out;
}


