.options-list li {
    margin-bottom: 5px;
}

.options-list li .radio input[type=radio] {
    height: 100%;
    width: 100%;
    z-index: 1;
}

.options-list li .radio .radio-label:before {
    background: #fff;
    border: 2px solid #4E69ED;
    border-radius: 100%;
    content: "";
    cursor: pointer;
    display: inline-block;
    height: 16px;
    margin-right: 12px;
    position: relative;
    text-align: center;
    top: 0.1em;
    transition: all .25s ease;
    vertical-align: top;
    width: 16px;
}

.options-list li .radio .radio-label:after {
    position: absolute;
    content: "";
    width: 8px;
    height: 8px;
    left: 8px;
    top: 11px;
    transform: translate(-50%, -50%);
    background: #F2F5FF;
    border-radius: 100%;
}

.options-list li .radio input[type=radio]:checked+.radio-label:before {
    box-shadow: inset 0 0 0 2px #f4f4f4;
}

.options-list li .radio input[type=radio]:checked+.radio-label:after {
    background-color: #4E69ED;
}

