/** Hotspot CSS Start **/

.hotspot-hide{
    display: none;
}


.hotspot-summary-section {
    padding-top: 15px;
    border-radius: 4px;
    position: absolute;
    width: 120px;
    left: -15px;
    opacity: 0;
    visibility: hidden;
    transform: translateY(5px);
    z-index: 99;
    transition: all 300ms ease;
}


.change-hotspot-position:hover .hotspot-summary-section {
    opacity: 1;
    visibility: visible;
    transform: translateY(0px);
}

.hotspot-summary-section.hotspot_top_ {
    padding-bottom: 15px;
    padding-top: 0;
    width: 120px;
    left: -15px;
    transform: translateY(-5px);
    bottom: 25px;
}
.hotspot-summary-section.hotspot_top_:before {
    content: '';
    left: 15px;
    border-radius: 4px;
    bottom: 10px;
    top: auto;
}
.hotspot-summary-section:before {
    content: '';
    width: 20px;
    height: 20px;
    background-color: #fff;
    position: absolute;
    top: 8px;
    left: 15px;
    border-radius: 4px;
    z-index: -1;
    transform: rotate(-60deg) skewX(-30deg) scale(1,.866);
}

.hotspot-summary-section.hotspot_left_ {
    left: auto;
    right: -15px;
}
.hotspot-summary-section.hotspot_left_:before {
    left: auto;
    right: 15px;
}
.hotspot-summary {
    background-color: #fff;
    padding: 10px;
    border-radius: 7px;
    width: 100%;
    box-shadow: 0 2px 2px rgb(0 0 0 / 20%);
}
.hotspot-summary-section.hotspot_top_ .hotspot-summary{
    box-shadow: 0 -2px 2px rgb(0 0 0 / 20%);
}
.hotspot-summary a {
    font-size: 12px;
    color: #545454;
    font-weight: 600;
    margin-bottom: 4px;
    text-align: left;
    text-decoration: none;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

.hotspot-summary a:hover {
    color: #2b2b2b;
}


.hotspot-summary h3 {
    text-align: left;
    margin: 0;
    font-size: 18px;
}


.hotspot-section,.customize-hotspot {
    width: 100%;
    height: 100%;
    position: absolute;
    background-repeat: no-repeat;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    z-index: 9;
}
span.change-hotspot-position {
    width: 25px;
    height: 25px;
    background: #2b2b2b;
    display: block;
    padding: 3px 4px 3px 3px;
    color: #000;
    border-radius: 100%;
}
span.change-hotspot-position.hotspot-hide {
    display: none;
}
.taggedAction {
    position: absolute;
    bottom: 6px;
}
.tb_author_media .symbol.symbol-circle {
    height: 100%;
    width: 100%;
    text-align: center;
    font-size: 22px;
    padding: 5px 0;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-color: #535353;
