.checkbox-squre {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    position: relative;
    text-align: left;
    cursor: pointer;
    font-size: 1rem;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
    margin: 0;
    font-size: 1rem;
    font-weight: 400;
    color: #333;
}

.checkbox-squre > input {
    position: absolute;
    z-index: -1;
    opacity: 0;
}

.sourceSection.init_sec .loginInnSec h3 {
