.form_cont_slides .carousel-indicators li {
    width: auto;
    height: auto;
    background-color: transparent;
    transition: all .3s;
    position: relative;
    border: none;
    display: none;
}

.form_cont_slides .carousel-indicators li.active {
    display: block;
}

.client_img:after {
    content: '';
    position: absolute;
    left: 0px;
    top: 0px;
    right: 0;
    width: 50px;
    height: 50px;
    z-index: 2;
    border-radius: 50%;
    transition: all .3s;
}

.form_cont_slides .carousel-indicators li.active .client_img:after {
	box-shadow: 0px 0px 0px 2px #fff;
    transition: all .3s;
}

