.sk_rect{
    height: 45px;
    width: 100%;
    background: #d9d9d9;
    border-radius: 3px;
    position: relative;
    overflow: hidden;
    background-color: #d9d9d9 !important;
}
.sk_img{
    height: 75px;
    width: 75px;
    background: #d9d9d9;
    border-radius: 50%;
    position: relative;
    overflow: hidden;
    background-color: #d9d9d9 !important;
}

.sk_line:before, .sk_img:before, .sk_rect::before, .sk_img_thumb:before{
    position: absolute;
    content: "";
    height: 100%;
    width: 100%;
    left:0;
    background-image: linear-gradient(to right, #d9d9d9 0%, rgba(0,0,0,0.05) 20%, #d9d9d9 40%, #d9d9d9 100%);
    background-repeat: no-repeat;
    background-size: 450px 400px;
    animation: shimmer 1s linear infinite;
}

.sk_img:before {
    background-size: 650px 600px;
}

@keyframes shimmer {
    0%{
      background-position: -450px 0;
    }
    100%{
      background-position: 450px 0;
    }
}

.sk_img_thumb {
    min-height: 150px;
    background: #d9d9d9;
    position: relative;
}

