    }

    .sk_loader_.stripe_card .card-img .sk_line {
        margin: 0 auto !important;
    }

    .sk_loader_.stripe_card .card-number .sk_line {
        margin: 20px auto 0px !important;
    }

    .sk_loader_.stripe_card .card-number .sk_line.sk_line_70{
        margin: 10px auto 15px !important;
        width: 50%;
    }

    .sk_loader_.stripe_card .add-card .label-inline .sk_line {
